import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { BtnSearch } from 'components/common/button';
import FormInfoCommon from './FormInfoCommon';
import TableStudentStudyAndExamAgain from './TableStudentStudyAndExamAgain';
import MenuTabTop from './MenuTabTop';
import './style.scss';

class WForcedStopSchoolPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Danh sách sinh viên học lại, thi lại">
        <Row className="rowMargin">
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Row style={{ textAlign: 'right' }}>
          <Col span={24}>
            <BtnSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableStudentStudyAndExamAgain />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <WForcedStopSchoolPage />
  </Page>
);



