import React, { Component } from 'react';
import { Table } from 'antd';
import { setStudentStudyAndExamAgain } from 'dump/data';

class TableStudentStudyAndExamAgain extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dayOfBirth',
        key: 'dayOfBirth',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'Ký hiệu',
        dataIndex: 'codeCourseName',
        key: 'codeCourseName',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'courseName',
        key: 'courseName',
      },
      {
        title: 'Số tín chỉ',
        dataIndex: 'numberOfCredits',
        key: 'numberOfCredits',
      },
      {
        title: 'Học kỳ',
        dataIndex: 'semester',
        key: 'semester',
      },
      {
        title: 'Năm học',
        dataIndex: 'schoolYear',
        key: 'schoolYear',
      },
      {
        title: 'Lần thi',
        dataIndex: 'timesExam',
        key: 'timesExam',
      },
      {
        title: 'Điểm thi lần 1',
        dataIndex: 'scoresExamFirst',
        key: 'scoresExamFirst',
      },
      {
        title: 'Điểm thi cao nhất',
        dataIndex: 'highestScores',
        key: 'highestScores',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentStudyAndExamAgain}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableStudentStudyAndExamAgain;



