import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime, courseNameOfStudyAndExamAgain } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Học kỳ:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Tổng hợp theo:">
              {getFieldDecorator('typeChoice', {})(
                <Select>
                  <Option value="1">Thi lại</Option>
                  <Option value="2">Học lại</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học phần:">
              {getFieldDecorator('courseName', {})(
                <Select>
                  {courseNameOfStudyAndExamAgain.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <p>
              Tổng số sinh viên: <b>3</b>
            </p>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



