import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
           
            <Menu.Item key="registerStudyAgain">
              <a href="#student/studyAndExamAgain/add/newClassAgain">
                <Icon type="highlight" theme="twoTone" twoToneColor="#ff3333" />
                Đăng ký học lại
              </a>
            </Menu.Item>
            <Menu.Item key="listRegisteredStudyAgain">
              <a href="#student/studyAndExamAgain/list/registered">
                <Icon type="down-circle" theme="twoTone" twoToneColor="#6699ff" />
                Danh sách đã đăng ký học lại
              </a>
            </Menu.Item>
            <Menu.Item key="printReport">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
              In báo cáo
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
              Xuất Excel
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



